import { nextTick } from 'vue'
import { createWebHistory, createRouter } from "vue-router";
import store from '../utils/store'
import Main from './Main.vue'
import ProjectView from './ProjectView.vue'

const routes = [
  {
    path: "/",
    name: 'Main',
    component: Main,
    meta: {
      title : "BYUNJISEUL",
    }
  },
  {
    path: '/:url',
    name: 'ProjectView',
    component: ProjectView,
    props: (route)=> route.params,
    meta: {
      title: "Project"
    }
  },
  {
    path: '/:pathMatch(.*)*', 
    name: 'not-found',
    component: Main
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

router.beforeEach(( to, from, next ) => {

  // spinner bottom-to-up 효과
  if(from.name !== undefined){
    document.querySelector('.smooth-scroll').classList.add('active');
  }

  store.commit('startSpinner',[to.name,from.name]);
  store.commit('changeSpinner', [to.path, from.name]);
  setTimeout(() => {
    document.documentElement.classList.add('scroll_lock');
    next();
  },1000);

});

router.afterEach((to)=> {

  setTimeout(() => {
    if(to.name !== 'Main'){
      store.commit('endSpinner');
    }
    if(store.state.userScroll){
      store.state.userScroll.scrollTo("top",{duration: 0, disableLerp: true});
    }
    window.scrollTo(0,0);
    document.documentElement.classList.remove('scroll_lock');
  }, 1000);
  
  /** title 변경 */
  let title = to.meta.title === undefined ? 'BYUNJISEUL' : to.meta.title;
  store.commit('projectPath',to.path.slice(1));
  nextTick(() => {
    if(to.meta.title == 'Project'){
      title = `Project - ${to.path.charAt(1).toUpperCase() + to.path.slice(2)}`;
      document.querySelector('.smooth-scroll').classList.add('in-project');
    }
    document.title = title;
  });

});

export default router; 
