<template>
  <ul class="work_list">
    <!-- work item -->
    <li class="work_item" v-for="(item,i) in $store.state.projectdata" :key="i" ref="workItem" :data-url="item.url.toLowerCase()">
      <!-- <router-link :to="`/${item.url}`" href=""> -->
      <div class="work_item_wrap" ><!--"-->
        <button @click="methods.changeProjectPath(item.url)" class="work_item_lnk" :data-color="item.color">  

          <div class="label_box">
            <div class="project_label">
              <span class="project_type">{{item.type}}</span>
              <span class="project_year">'{{item.year}}</span>
            </div>
          </div>

          <div class="thumb_box">
            <div class="thumb">
              <img :src="item.thumb" alt="">
            </div>
          </div>

          <div class="txt_box">
            <div class="project_title">
              <strong>{{item.title}}</strong>
            </div>
            <p class="project_case">{{item.case}}</p>
          </div>

        </button>
      </div>
      <!-- </router-link> -->
    </li>

  </ul>
</template>

<script>
import { useRouter,onBeforeRouteUpdate } from 'vue-router'

export default {
  name: 'MainWorkList',
  setup(){
      const router = useRouter();
      const methods = {
        changeProjectPath: (url)=>{
          router.push({path: '/' + url});
        }
      };
      onBeforeRouteUpdate((to, from, next) => {
        next();
      });
      return{
        methods
      }
  },
}
</script>