import { createStore } from 'vuex'
import projectdata from '../data/projectdata.js'

const store = createStore({
  state(){
    return {
      projectdata: projectdata,
      loadingStatus: false,
      userScroll: null,
      isMobile: false,
      currPath: "/",
      currData: "",
      pageTitle: "BYUNJISEUL",
      currPageType: "main",
      currSpinnerType: "spinner",
      domHeight: document.documentElement.clientHeight,
      screenHeight: window.screen.height,
    }
  },
  mutations:{
    startSpinner(state, name){
      state.loadingStatus = true;
      let [ to, from ] = name;
      if( to === "ProjectView" ){
        state.currPageType = "project";
        if(from !== undefined){
          state.currSpinnerType = "project_spinner";
        }
      }else if(from === undefined || to === "Main"){
        state.currPageType = "main";  
        state.currSpinnerType = "spinner";
      }
    },
    changeSpinner(state, target){
      let [ toPath, fromName ] = target;
      if(fromName !== undefined){
        const tg = state.projectdata.find(x=>x.url === toPath.slice(1));
        state.pageTitle = tg ? tg.title : "BYUNJISEUL";
        return;
      }
      state.pageTitle = "BYUNJISEUL";
    },
    endSpinner(state){
      state.loadingStatus = false;
    },
    mobileAgent(state, val){
      state.isMobile = val;
    },
    projectPath(state, target){
      if(target){
        state.currPath = target;
        state.currData = state.projectdata.find(x=>x.url === target);
      }
    },
  },
})

export default store