import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";
import { gsap } from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"
import store from "../../utils/store"

const makeCanvas = function(){
	let elem = document.querySelector(".intro_canvas");
	let camera, scene, renderer;

	let mouseX = 0;
	let mouseY = 0;
	let targetX = 0;
	let targetY = 0;

	init();

	function init() {
		const winWidth = elem.clientWidth;
		const winHeight = elem.clientHeight;
		
		camera = new THREE.PerspectiveCamera( 50, winWidth / winHeight, 1, 100);
		camera.aspect = winWidth / winHeight;
		camera.position.set(0,0.25,18);
		camera.scale.z = 1.5;

		scene = new THREE.Scene();

		renderer = new THREE.WebGLRenderer( { alpha: true, antialias: true } );
		renderer.gammaOutput = true;
		renderer.outputEncoding = THREE.sRGBEncoding;
		renderer.setPixelRatio( window.devicePixelRatio );
		renderer.setSize( winWidth, winHeight );
		elem.appendChild( renderer.domElement );

		const loader = new GLTFLoader();
		const dracoLoader = new DRACOLoader();
		dracoLoader.setDecoderPath("https://www.gstatic.com/draco/versioned/decoders/1.4.3/" );
		dracoLoader.setDecoderConfig({ type: "js" }); 
		dracoLoader.preload();
		loader.setDRACOLoader(dracoLoader);

		const rightLight = new THREE.DirectionalLight(0xeeeeeee, 1);
		rightLight.position.set(7,4,4);

		const leftLight = new THREE.DirectionalLight(0xeeeeeee, 1);
		leftLight.position.set(-12,3,4);

		scene.add(rightLight);
		scene.add(leftLight);

		const material = new THREE.MeshStandardMaterial({
			color:  new THREE.Color(0x518100),
			emissive: new THREE.Color(0x517100),
			roughness: 0,
		});

		loader.load("./images/byun.glb", function ( gltf ) {
			const geometry = gltf.scene.children[ 0 ].geometry; 
			let mesh = new THREE.Mesh( geometry, material); 

			scene.add(mesh);	
			setTimeout( onStart, 1000 );
		});

		// EVENTS
		document.addEventListener( "mousemove", onDocumentMouseMove );
	}

	function onStart(){
		store.commit("endSpinner");
		
		const titleText = gsap.utils.toArray('.intro_summary span');
		const canvasArea = document.querySelector(".intro_render");
		canvasArea.style.opacity = 1;
		canvasArea.style.transform = "scale(1)";

		gsap.to(titleText,{
			x: 0, 
			y: 0,
			duration: 1,
			opacity: 1,
		});

		gsap.to('h1 a',{
			scrollTrigger: {
				trigger: '.intro',
				start: 'center top',
				invalidateOnRefresh: true,
			},
			duration: 0.3,
			opacity: 1,
			y: 0,
		});
	}

	function onDocumentMouseMove( event ) {
		mouseX = event.clientX - window.innerWidth / 2;
		mouseY = event.clientY - window.innerHeight / 2;
	}

	// resize 
	ScrollTrigger.addEventListener("refreshInit", () => {
		setTimeout(() => {
			const winWidth = elem.clientWidth;
			const winHeight = elem.clientHeight;
	
			camera.aspect = winWidth / winHeight;
	
			camera.updateProjectionMatrix();
			renderer.setSize( winWidth, winHeight );
		}, 120);
	});

	/** gsap에서 rap 역할 */
	gsap.ticker.add(() => {
		render();
	});

	function render() {
		const rect = elem.getBoundingClientRect();
		let { left, right, top, bottom } = rect;
		
		targetX = mouseX * 0.0005;
		targetY = mouseY * -0.0005;

		const isOffscreen =
				bottom < 0 ||
				top > window.innerHeight ||
				right < 0 ||
				left > window.innerWidth;

		scene.traverse( function ( child ) {
			if ( child.isMesh && !isOffscreen ) {
				
				if(window.innerWidth < 768) {
					child.rotation.y = Math.sin(performance.now()/ 1000) * 0.5 - 0.5;
					child.rotation.x = 0;
				}else{
					child.rotation.y += 0.05 * ( targetX - child.rotation.y );
					child.rotation.x += 0.05 * (-targetY - child.rotation.x );
				}

			}
		});

		renderer.render( scene, camera );
	}

}

export default makeCanvas