<template>
  <div class="all_wrap" :id="$store.state.currPageType" >
    <the-header></the-header>
    <div class="smooth-scroll" data-scroll-container>
      <router-view />
    </div>
    <transition name="fade">
      <the-spinner :loading="$store.state.loadingStatus" />
    </transition>
  </div>
</template>

<script>
import { onMounted } from 'vue';
import { useStore } from "vuex";
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import TheHeader from './components/TheHeader.vue'
import TheSpinner from './components/TheSpinner.vue'
import LocomotiveScroll from 'locomotive-scroll';
gsap.registerPlugin(ScrollTrigger);

export default {
  name: 'App',
  components: {
    TheHeader,
    TheSpinner,
  },
  setup() {
    const store = useStore();
    const methods = {
      setVh: ()=>{
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
      },
      mobileChk: ()=>{
        const isMobile = /Mobile|iPhone|iPad|iPod|Android|BlackBerry|Opera Mini|IEMobile/i.test(navigator.userAgent) || (/Macintosh/i.test(navigator.userAgent) && navigator.maxTouchPoints > 1);
        store.commit("mobileAgent", isMobile? true : false);
      },
      scrollInit: ()=>{
        if(store.state.isMobile === true) return;
        const locoScroll = new LocomotiveScroll({
          el: document.querySelector(".smooth-scroll"),
          smooth: true,
          tablet: { breakpoint: 0 },
          smartphone: { breakpoint: 0 },
        });
     
        locoScroll.on("scroll", (e) => {
          if(store.state.loadingStatus === "true") return;
          if(e.scroll.y < 0) return;
          ScrollTrigger.update(e);
        });
          
        ScrollTrigger.scrollerProxy(".smooth-scroll", {
          scrollTop(value) {
            return arguments.length ? locoScroll.scrollTo(value, {duration: 0, disableLerp: true}) : locoScroll.scroll.instance.scroll.y;
          }, 
          getBoundingClientRect() {
            return {top: 0, left: 0, width: window.innerWidth, height: window.innerHeight};
          },
          pinType: document.querySelector(".smooth-scroll").style.transform ? "transform" : "fixed"
        });

        ScrollTrigger.defaults({ scroller: ".smooth-scroll" });

        ScrollTrigger.addEventListener("refresh", () => locoScroll.update());
        ScrollTrigger.refresh();

        store.state.userScroll = locoScroll;
      },
    };

    onMounted(()=>{
      methods.setVh();
      window.addEventListener('resize',()=> methods.setVh());
      methods.mobileChk();
      methods.scrollInit();
    });
    
    return{
      methods
    }
  }
}
</script>