<template>
  <section class="work">

    <!-- s: work heading ( 타이틀 및 작업물 이미지 ) -->
    <div class="work_preview">

      <div class="work_images">
        <div class="work_images_item" v-for="(list,i) in data.workimgs" :key="i">
          <div class="work_images_box">
            <img :src="item.image" :alt="item.alt" v-for="(item,idx) in list" :key="idx">
          </div>
        </div>
      </div>

      <div class="work_heading">
        <div class="work_title">
          <h3 class="line_wrap"><span class="">WORK</span></h3>
          <p class="work_desc">
            웹 에이전시 근무 경력과 마케팅 대행사 근무 경력으로 <br>
            비즈니스 웹 사이트, 쇼핑몰, 프로모션 사이트 등
            다양한 형태의 퍼블리싱 작업을 진행했습니다.<br>
            다음은 몇가지 유형의 프로젝트 입니다.<br>
          </p>
        </div>
      </div>

    </div>
    <!-- e: work heading ( 타이틀 및 작업물 이미지 ) -->

    <!-- s: work content -->
    <div class="work_content" id="work_content" data-scroll>
      
      <!-- s: sticky text -->
      <div class="work_content_info" data-scroll data-scroll-sticky data-scroll-target="#work_content">
        <div class="marquee_wrap">
          <div class="marquee_text">
            <div class="text" data-bgurl="lghomin">
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
              <span>PROJECT CASE </span>
            </div>
          </div>
        </div>
      </div>
      <!-- e: sticky text -->

      <!-- s: work list -->
      <main-work-list></main-work-list>
      <!-- e: work list -->

    </div>
    <!-- e: work content -->

  </section>
</template>

<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'
import MainWorkList from './MainWorkList.vue'
import workimgs from '../data/workpreviewimgs.js'

export default {
  name: 'MainWork',
  components: { 
    MainWorkList 
  },
  setup(){
    const data = {
      workimgs: workimgs
    };
    const methods = {
      workPreview : ()=>{
        const workPic = document.querySelectorAll('.work_images_box');
        workPic.forEach((pic) =>{

          pic.appendChild(pic.querySelector("img").cloneNode(true));
          
          const images = Array.from(pic.querySelectorAll('img'));
          const firstOutList = images.slice(1);
          const duration = 0.75;
          const pause = 1.5;
          const stagger = duration + pause;

          gsap.to('.work_images_item',{
            scrollTrigger: {
              trigger: '.work_preview',
              start: 'top center',
              duration: 1.2,
            },
            stagger: 0.2,
            ease: "none",
            clipPath: "polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%)"
          });
          
          const tl = gsap.timeline({
            scrollTrigger: {
              trigger: '.work_preview',
              start: 'top center',
              end: 'bottom top',
            },
            repeat: -1,
          });

          tl.from(firstOutList,{
            '--clip-scale-x1': '0%',
            '--clip-scale-x2': '-40%',
            duration: duration,
            stagger: {
              each: stagger,
            },
            ease: 'power2.out',
          },0).to(firstOutList,{
            '--clip-scale-x1': '100%',
            '--clip-scale-x2': '100%',
            duration: duration,
            stagger: {
              each: stagger,
            },
            ease: 'power2.out',
          },pause);

        });
      },

      listOffScreen: ()=>{
        ScrollTrigger.create({
          trigger: '.work_content',
          start: 'top bottom',
          end: 'bottom top',
          toggleClass: 'ani',
          invalidateOnRefresh: true,
        });
      },
    }

    onMounted(()=>{
      methods.workPreview();
      methods.listOffScreen();
    });
    
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return{
      data,
      methods
    }
  }
}
</script>