<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

export default {
  name: 'MainSkill',
  setup(){
    const methods = {
      skillShow: () => {
        gsap.to('.skill_list li',{
          scrollTrigger: {
            trigger: '.skill_content',
            start: 'top 85%',
            end: 'bottom center',
            scrub: 2,
            invalidateOnRefresh: true,
          },
          backgroundPositionX: 0,
          duration: 0.7,
          stagger: 0.7,
        });
      }
    };

    onMounted(()=>{
        methods.skillShow();
    });
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return{
      methods
    }
  },
}
</script>