<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

export default {
  name: 'MainAbout',
  setup(){
    const methods = {
      aboutContShow: ()=>{
        const conts = gsap.utils.toArray('.about_cont');
        conts.forEach(cont =>{
          let tit = cont.querySelector('.about_cont_title');
          let txt = cont.querySelector('.about_cont_text');
          const tl = gsap.timeline({
            scrollTrigger:{
              trigger: cont,
              start: 'top 85%',
              duration: 1, 
              invalidateOnRefresh: true,
            }
          });
          tl.to([tit, txt],{
            x: 0,
            y: 0,
            opacity: 1,
          });
        });
      }

    };
    onMounted(()=>{
      // let mm = gsap.matchMedia();
      
      // mm.add({
      //   isMobile: "(max-width: 768px)",
      //   isTablet: "(min-width: 769px)"
      // }, () => {  //context
      //   // let { isMobile } = context.conditions;
      //   // methods.aboutTitleShow(isMobile? ['top 75%', false] : ['top bottom', 1]);
      //   methods.aboutContShow();
      // });
       methods.aboutContShow();

    });
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return {
      methods
    }
  }
}
</script>