<template>
  <section class="contact">
    <div class="contact_wrap">

      <div class="contact_title_wrap">
        <div class="contact_title">
          <h3>(<i class="arr"></i>)CONTACT</h3>
        </div>
      </div>

      <div class="contact_content">
        <div class="contact_pic_wrap">
          <div class="contact_pic">   
            <img src="@/assets/images/pic_byunjiseul.jpeg" alt="">
          </div>
        </div>
        <div class="contact_info">
          <ul>
            <li class="name">BYUNJISEUL</li>
            <li class="call"><a href="tel:01054389944">01054389944</a></li>
            <li><a href="mailto:xylicho15@gmail.com">xylicho15@gmail.com</a></li>
          </ul>
        </div>
      </div>
     
      <div class="copyright">
        &copy;2022
      </div>
    </div>
  </section>
</template>

<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

export default {
  name: 'MainContact',
  setup(){
    const methods = {
      contactPicMove : ()=>{
        let tl = gsap.timeline({
          scrollTrigger:{
            trigger: '.contact',
            start: 'top bottom',
            end: 'bottom bottom',
            scrub: 1,
            invalidateOnRefresh: true,
          },
        });
        tl.to('.contact_pic_wrap img',{
          keyframes:[{
            scale: 1,
            ease: 'ease4.out',
          },
          {
            y: "-3%",
            ease: 'none',
          }]
        });
      }
    };

    onMounted(()=>{
      methods.contactPicMove();
    });
    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return {
      methods
    }
  }
}
</script>