<template>
  <div class="sub"> 

    <!-- s: project -->
    <article class="project_view"> 
      <div class="project_wrap">
       
        <h2 class="project_title"><span>{{data.currProject.title}}</span></h2>

        <div class="project_info"> 

          <dl class="project_summary">
            <dt class="blind">(작업유형)</dt>
            <dd>{{data.currProject.type}}</dd>
            <dt class="blind">(사용스킬)</dt>
            <dd>{{data.currProject.skill}}</dd>
            <dt class="blind">(기여도)</dt>
            <dd>{{data.currProject.contribution}}</dd>
            <dt class="blind">작업년도 :</dt>
            <dd>{{data.currProject.year}}</dd>
          </dl>
          
          <div class="project_desc">
            <p v-html="dataDesc"></p>
          </div>
        </div>
        
      </div>
      <!-- view -->
      <div class="project_detail">
        <div class="content_wrap">
          <div class="detail_img_grid">

            <div v-for="(item,i) in data.currProject.imgs" :key="i" class="detail_img_item">
              <figure>
                <div class="detail_img_box detail_figure" v-if="item.figure" >
                    <img :src="item.url" :data-url="item.url" alt="">
                    <div class="figure_box" :class="item.class">
                      <figure :style="`position:relative;height:0;padding-top:${item.figHeight};`">
                        <iframe :src="`https://player.vimeo.com/video/${item.figUrl}&autoplay=1&loop=1&title=0&byline=0&portrait=0&background=1`" frameborder="0" allow="autoplay; fullscreen; picture-in-picture" allowfullscreen v-if="item.figType === 'video'"></iframe>
                        <img :src="item.figUrl" alt="" v-if="item.figType === 'gif'">
                      </figure>
                    </div>
                </div>
                <div class="detail_img_box" v-else>
                  <img :src="item.url" :data-url="item.url" alt="">
                </div>
              </figure>
            </div>

          </div>

        </div>
      </div>
    </article>
    <!-- e: project -->

    <!-- s: btn -->
    <div class="lnk_area">

      <div class="lnk_other"> 
        <div class="lnk_wrap lnk_prev">
          <button @click="methods.changeProjectPath(data.prevProject.url)" v-if="data.prevProject">
            <div class="lnk_text">
              <span class="lnk_arr">←</span>PREV
            </div>
            <div class="thumb_box">
              <div class="thumb">
                <img :src="data.prevProject.thumb" alt="">
              </div>
            </div>
          </button>
        </div>

        <div class="lnk_main">
          <button @click="methods.changeProjectPath('/')">
            <span class="lnk_text">MAIN</span>
          </button>
        </div>

        <div class="lnk_wrap lnk_next">
          <button @click="methods.changeProjectPath(data.nextProject.url)"  v-if="data.nextProject">
            <div class="lnk_text">
              NEXT<span class="lnk_arr">→</span>
            </div>
            <div class="thumb_box">
              <div class="thumb">
                <img :src="data.nextProject.thumb" alt="">
              </div>
            </div>
          </button>
        </div>
      </div>
      
    </div>
    <!-- e: btn -->

  </div>
</template>

<script>
import '@/assets/scss/project.scss'
import { ref, computed, onMounted, onUpdated } from 'vue'
import { useStore } from "vuex"
import { useRouter,onBeforeRouteUpdate } from "vue-router"

export default {
  name: 'Project',
  props:{
    url: String,
  },
  
  setup(){
    const store = useStore();
    const router = useRouter();
    const dataDesc = computed(()=> store.state.currData.desc.replaceAll("\n","<br>"));

    const data = ref({
      currProject: store.state.currData,
      prevProject: null,
      nextProject: null,
      currId: store.state.currData.id,
      prevId: 0,
      nextId: 0,
      // tgTitle: null,
    });
    const methods = {
      projectInit(){
        data.value.currProject = store.state.currData;
        data.value.currId = store.state.currData.id;
        data.value.prevId = data.value.currId + 1;
        data.value.nextId = data.value.currId - 1;
        data.value.prevProject = data.value.prevId > store.state.projectdata.length ? store.state.projectdata.find(x => x.id === 1) : store.state.projectdata.find(x => x.id === data.value.prevId);
        data.value.nextProject = data.value.nextId === 0 ? store.state.projectdata.find(x => x.id === store.state.projectdata.length) : store.state.projectdata.find(x => x.id === data.value.nextId);
      },
      changeProjectPath( url ){
        if(url === '/'){
          router.push({path: '/'});
        }else{
          router.push({path: '/' + url});
        }
      },
    }; 
    onBeforeRouteUpdate ((to, from, next) => {
      next();
    });
    onMounted(()=>{
      methods.projectInit();
    });
    onUpdated(()=> {
      methods.projectInit();
    });
    return{
      data,
      methods,
      dataDesc
    }
  }
}
</script>
