    <div class="playground_info">

      <div class="playground_desc">
        <p>
          사용자의 이목을 끌게 하는 인터랙티브 웹은 언제나 저의 흥미를 유발합니다.<br class="line_break_pc">
          사용자 경험에 특별함을 더해주는 3D 인터랙티브 웹을 구현하기 위해 Three.js를 공부하고 있습니다.
