</template>

<script>
import { onMounted, onBeforeUnmount } from 'vue'
import { gsap } from 'gsap'
import { ScrollTrigger } from 'gsap/ScrollTrigger'

export default {
  name: 'Mainplayground',
  setup(){
    const methods = {
      horizonScroll: ()=>{
        const horizonContainer = document.querySelector(".playground_container");
        const horizon = document.querySelector(".playground_box");
        const videos = document.querySelectorAll(".playground_item");
        let tl = gsap.timeline({
          scrollTrigger: {
            trigger: horizonContainer,
            start: 'top top',
            end: '80% bottom',
            scrub: 2,
            invalidateOnRefresh: true,
            onRefresh: ()=>{
              ScrollTrigger.update()
            }
          },
        });
        tl.to(horizon,{
          x: ()=> - (horizon.scrollWidth - window.innerWidth),
          ease: 'none',
        }).to(videos,{
          xPercent: -40,
        },0);
      }
    };
    onMounted(()=>{
      methods.horizonScroll();
    });

    onBeforeUnmount(()=>{
      ScrollTrigger.getAll().forEach((t) => t.kill());
    });

    return {
      methods
    }
  }
}
</script>
